@import "variables.less";

.jqx-widget-header {
  font-size: 14px;
  color: #474747;
}

.jqx-grid{
  border-radius: 7px;
  box-shadow: 0 1px 3px 0 #888888;
}

.jqx-widget-content {
  font-size: 14px;
  color: #1B1B1B;
  .checkbox-small{
    margin-top: 2px;
    margin-left: 8px;
  }
}

.jqx-grid-cell {
  >div {
    padding-left: 4px !important;
    padding-right: 4px !important;}
  >img {text-align: center;}
}

.jqx-fill-state-normal {
  border-color: #adadad;
  background: #fff;}

.jqx-cell{
  line-height: 26px;
  padding: 0 8px 0 8px;
}

.jqx-grid-cell-alt {  background: @n-table-row-grey;}
.jqx-widget-header{background: linear-gradient(@n-table-th-top,@n-table-th-bottom); font-weight: bolder;}

.grid-alternating-rows{
  .jqx-grid-cell {
    border-width: 0 1px 0 0;
  }

  //checkbox
  .jqx-rc-all {
    border-radius: 4px;
    border: 1px solid #b2b2b2;
  }

