.n-login-screen {
  height: 100%;
  #gradient > .radial(@gradient-background-radial-center, @gradient-background-radial-far-side);
  background-repeat: repeat;
  padding: 0;
}

.n-login {
  position: absolute;
  top: 50%;
  left: 50%;
  width: @n-login-width;
  min-height: @n-login-height;
  margin-left: @n-login-margin-left;
  margin-top: @n-login-margin-top;

  .n-login-logo {
    text-align: right;
    padding-right: 5px; // 5 from dialog edge
    margin-bottom: 43px; // 43 distance to version and system info
  }

  .panel {
    margin: 0 auto;
    padding: 0 28px;

    button {
      margin: 0;
    }

    .n-login-title {
      height: 43px;
      padding-top: 20px;

      .n-product-name-blue {
        color: #124191;
      }
    }

    .panel-body {
      background: none;

      h2.n-login-title-version-light {
        height: 26px;
        margin: 0;
        padding-top: 10px;
        color: #474747;
        font-weight: 300;
      }

      .n-login-product-name {
        margin: 36px 0 -16px;
        color: #474747;
        font-size: 14px;
      }

      .alert-error {
        display: none;
        padding-left: 5px;
        padding-bottom: 20px;
        > .icon:first-child {
          top: 3px;
          position: relative;
          font-size: @font-size-medium;
          margin-right: 5px;
        }
      }

      .n-login-panel-fields {
        padding: 36px 0 20px 0;
        overflow: hidden;

        .input-required {
          padding: 0px 5px 0px 0;

          &:not(:first-child) {
            padding-left: 5px;
          }
        }

        .n-login-textfields {
          padding: 0;
          margin: 0;

          .form-control-feedback {
            padding-top: 1px;
            right: 4px !important;
          }
        }
      }
    }

    .n-login-footer {
      overflow: hidden;
      padding-bottom: 20px;

      .n-login-forget-password {
        display: inline-block;
        line-height: 32px;

        a:hover, a:active, a:focus {
          border-bottom: 1px solid #00A1CC;
          color: #0B6DAF;
        }

        .icon-breadcrumb {
          font-size: 50%;
          font-weight: bold;
          &:after {
            color: #1b1b1b;
          }
        }
      }

      button {
        float: right;
      }
    }
  }
}

.n-login-copyright {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 18px;
  text-align: center;
  color: @n-login-text-color;
}

.n-login-product {
  min-height: 318px;
  margin-top: -159px;
}

.n-login-signup {

  min-height: 350px;
  margin-top: -175px;

  .n-login-sign-up-bar {
    border-top: 1px solid #D8D8D8;
    padding: 20px 0;

    span {
      color: #474747;
      font-size: 14px;
      line-height: 32px;
    }

  }
}

.n-login-legal-copy {
  min-height: 358px;
  margin-top: -179px;

  .n-login-legal-copy-text {
    border-top: 1px solid #D8D8D8;
    color: #474747;
    font-size: 11px;
    padding: 14px 0;
  }
}

.n-login-legal-acceptance {
  min-height: 296px;
  margin-top: -148px;

  .n-login-panel-fields {
    color: #474747;
    font-size: 12px;
  }
}

.n-login-with-four-column {
  top: auto;
  bottom: 354px;
  margin-top: 0;
}

.n-login-four-column {
  position: absolute;
  top: calc(100% - 324px);
  width: 100%;

  .n-login-column-panel {
    display: inline-block;
    position: absolute;
    right: calc(50% + 10px);

    &:last-child {
      right: auto;
      left: calc(50% + 10px);
    }

    h1 {
      font-size: 20px;
      color: #1b1b1b;
    }

    .n-login-column {
      vertical-align: top;
      display: inline-block;
      width: 200px;
      max-height: 122px;
      color: #474747;
      font-size: 12px;

      &:not(:last-child) {
        margin-right: 20px;
      }
    }
  }

}